import { mapActions } from 'vuex';
import baseMixin from '@/mixins'
export default {
	name:'indexMenu',
	mixins:[baseMixin],
	data() {
		return {
		     allMenuList:[],//功能菜单列表
		}
	},







	onReady () {
		this.allMenuList = this.menuList
	},

	methods: {
		//跳转到指定页面
		turnToPage(_url){
			//debugger
			switch (_url){
				case 'compfactorykanban':
					uni.navigateTo({
						url: '../report/compfactorykanban/compfactorykanban'
					});
					break;
				case 'deliveryQuery':
				uni.navigateTo({
					url: '../report/deliveryquery/deliveryquery'
				});
					break;
				case 'PaperOrderQuery':
				uni.navigateTo({
					url: '../report/paperorderquery/paperorderquery'
				});
					break;
				case 'SumOfCustArrears':
				uni.navigateTo({
					url: '../report/sumofcustarrears/sumofcustarrears'
				});
					break;
				default:
				    uni.showToast({
				    	title:'找不到指定路径，请确认！',
						icon:'none',
						duration:2000
				    })
					break;
			}
		}

	}
}
